.container {
  transition: all .3s ease;
  flex: 1;
  min-height: 20vh;
  margin: 20px 20px;
  margin-left: calc(var(--sidebar-width) + 20px);
  align-items: center;
}

.textbox {
  width: 100%;
  padding: 20px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0px 0px 4px 0px #000;
}